@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
  overscroll-behavior: none;
  margin: 0;
  padding: 0;
  height: 100%;
}
/* navbar.css */

nav {
  background-color: rgba(26, 26, 26, 0.95); /* Matches home background with slight transparency */
  color: #ffffff;
  padding: 20px 40px;
  text-align: center;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
  box-sizing: border-box;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(100, 255, 218, 0.1); /* Subtle cyan border */
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  grid-gap: 3rem;
  gap: 3rem;
}

nav li {
  margin: 0;
  position: relative;
}

nav a {
  color: #ffffff;
  text-decoration: none;
  font-family: 'Roboto Mono', monospace;
  font-size: 1.1rem;
  transition: color 0.3s ease;
  padding: 0.5rem 1rem;
  position: relative;
}

nav a:hover {
  color: #64ffda; /* Cyan accent color */
}

nav a.active {
  color: #64ffda;
}

nav a::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #64ffda;
  transition: width 0.3s ease;
}

nav a:hover::after,
nav a.active::after {
  width: 100%;
}

/* Responsive styles */
@media (max-width: 768px) {
  nav {
    padding: 15px 20px;
  }

  nav ul {
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }

  nav a {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  nav ul {
    grid-gap: 1rem;
    gap: 1rem;
  }

  nav a {
    font-size: 0.9rem;
    padding: 0.3rem 0.6rem;
  }
}
/* home.css */

#home {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding: 80px 20px 20px; /* Added top padding to account for navbar */
    box-sizing: border-box;
    background-color: #1a1a1a;
    color: #ffffff;
    overflow: hidden; /* Prevents extra scroll */
  }

.content {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.text-container {
  text-align: center;
}

.typewriter-text {
  margin-bottom: 2rem;
}

.typed-line {
  font-size: 2.5rem;
  font-family: 'Roboto Mono', monospace;
  margin-bottom: 0.5rem;
  min-height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
}

.cursor {
  display: inline-block;
  width: 3px;
  margin-left: 5px;
  background-color: #ffffff;
  animation: blink 1s step-end infinite;
}

.phrase-container {
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2rem 0;
}

.single-phrase {
  font-size: 2rem;
  color: #64ffda;
  font-family: 'Roboto Mono', monospace;
  margin: 0;
  display: flex;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
  transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
}

.fade-out {
  opacity: 0;
  transform: translateY(20px);
}

.fade-in {
  opacity: 1;
  transform: translateY(0);
}

.resume-link-container {
  margin-top: 3rem;
}

.resume-link {
  background: none;
  border: 2px solid #64ffda;
  color: #64ffda;
  padding: 0.75rem 1.5rem;
  font-family: 'Roboto Mono', monospace;
  font-size: 1.2rem;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.resume-link:hover {
  background-color: rgba(100, 255, 218, 0.1);
}

.resume-text {
  position: relative;
  z-index: 1;
}

@keyframes blink {
  from, to {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}

/* Responsive styles */
@media (max-width: 768px) {
  .typed-line {
    font-size: 2rem;
  }
  
  .single-phrase {
    font-size: 1.5rem;
  }
  
  .resume-link {
    font-size: 1rem;
    padding: 0.6rem 1.2rem;
  }
}

@media (max-width: 480px) {
  .typed-line {
    font-size: 1.6rem;
  }
  
  .single-phrase {
    font-size: 1.2rem;
  }
  
  .resume-link {
    font-size: 0.9rem;
    padding: 0.5rem 1rem;
  }
}
/* intro.css */

#intro {
  background-color: #1a1a1a; /* Ensure this is applied */
  color: #ffffff;
  min-height: 100vh;
  padding: 100px 20px 40px;
  box-sizing: border-box;
}

.intro-container {
  max-width: 1200px;       /* limits the width on large screens */
  margin: 0 auto;          /* centers the container horizontally */
  display: flex;           /* flex layout to stack child elements */
  flex-direction: column;  /* vertical stacking */
  grid-gap: 4rem;
  gap: 4rem;               /* spacing between sections (timeline, skills, etc.) */
  padding: 0 20px;         /* optional horizontal padding for small screens */
  box-sizing: border-box;  /* include padding in width calculations */
}

.section-title {
  font-family: 'Roboto Mono', monospace;
  font-size: 2rem;
  color: #64ffda;
  margin-bottom: 2rem;
  text-align: center;
}

/* Timeline Styles */
.timeline {
  display: flex;
  flex-direction: column;
  grid-gap: 2rem;
  gap: 2rem;
}

.timeline-item {
  border-left: 2px solid #64ffda;
  padding-left: 2rem;
  position: relative;
  padding-bottom: 2rem;
}

.timeline-item::before {
  content: '';
  position: absolute;
  left: -8px;
  top: 0;
  width: 14px;
  height: 14px;
  background-color: #64ffda;
  border-radius: 50%;
}

.timeline-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.timeline-header h3 {
  margin: 0;
  font-size: 1.25rem;
  color: #ffffff;
}

.period {
  font-family: 'Roboto Mono', monospace;
  color: #64ffda;
  font-size: 1rem;
  display: block;
  margin-top: 0.5rem;
}

.timeline-details {
  margin: 0;
  padding: 0;
  list-style: none;
}

.timeline-details li {
  margin: 0.5rem 0;
  line-height: 1.6;
  position: relative;
  padding-left: 1.5rem;
  color: #ffffff;
}

.timeline-details li::before {
  content: '▹';
  position: absolute;
  left: 0;
  color: #64ffda;
}

/* Skills Grid Styles */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  grid-gap: 2rem;
  gap: 2rem;
  padding: 2rem;
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  border: 1px solid rgba(100, 255, 218, 0.2);
}

.skill-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  transition: all 0.3s ease;
  padding: 1rem;
  background-color: rgba(255, 255, 255, 0.25);
  border-radius: 8px;
}

.skill-item:hover {
  transform: translateY(-5px);
  background-color: rgba(255, 255, 255, 0.15);
}

.skill-icon {
  width: 50px;
  height: 50px;
  transition: transform 0.3s ease;
}

.skill-item:hover .skill-icon {
  transform: scale(1.1);
}

.skill-name {
  font-family: 'Roboto Mono', monospace;
  font-size: 0.9rem;
  color: #64ffda;
  font-weight: 500;
}

/* Responsive Styles */
@media (max-width: 768px) {
  #intro {
    padding: 80px 15px 30px;
  }

  .section-title {
    font-size: 1.75rem;
  }

  .timeline-header h3 {
    font-size: 1.25rem;
  }

  .skills-grid {
    grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
    grid-gap: 1.5rem;
    gap: 1.5rem;
    padding: 1.5rem;
  }

  .skill-item {
    padding: 0.75rem;
  }
}

@media (max-width: 480px) {
  .section-title {
    font-size: 1.5rem;
  }

  .timeline-item {
    padding-left: 1.5rem;
  }

  .skills-grid {
    grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));
    grid-gap: 1rem;
    gap: 1rem;
  }

  .skill-icon {
    width: 40px;
    height: 40px;
  }

  .skill-name {
    font-size: 0.8rem;
  }
}

/* Add toggle interaction */
.timeline-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer; /* make clickable */
  transition: background-color 0.2s ease;
  padding: 0.5rem 0;
}

.timeline-header:hover {
  background-color: rgba(255, 255, 255, 0.05);
}

.timeline-title {
  display: flex;
  flex-direction: column;
}

.toggle-icon {
  font-size: 1.25rem;
  font-weight: bold;
  color: #64ffda;
  margin-left: 1rem;
}

/* Start hidden, expand with animation */
.timeline-details {
  margin-top: 0.5rem;
  padding-left: 1.5rem;
  list-style: none;
  max-height: 0;
  overflow: hidden;
  opacity: 0;
  transition: all 0.3s ease-in-out;
}

.timeline-item.open .timeline-details {
  max-height: 500px; /* large enough to fit details */
  opacity: 1;
}

/* Projects Section */
#projects {
  display: block; /* ✅ ensures children stack vertically */
  background-color: #1a1a1a;
  color: #ffffff;
  min-height: 100vh;
  padding: 100px 0px 40px;
  box-sizing: border-box;
}


.projects-container {
  max-width: 100%; /* ✅ no limit */
  margin: 0 auto;
  width: 100%;
}

.section-title {
  font-family: 'Roboto Mono', monospace;
  font-size: 2rem;
  color: #64ffda;
  margin-bottom: 2rem;
  text-align: center;
}

/* ✅ Grid: 3 on large, 2 on tablet, 1 on mobile */
.projects-grid {
  display: grid;
  grid-gap: 2rem;
  gap: 2rem;
  width: 100%;
  grid-template-columns: repeat(3, 1fr); /* ✅ default: 3 cards */
}

@media (max-width: 1024px) {
  .projects-grid {
    grid-template-columns: repeat(2, 1fr); /* tablet */
  }
}

@media (max-width: 640px) {
  .projects-grid {
    grid-template-columns: 1fr; /* mobile */
  }
}

.project-card {
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid rgba(100, 255, 218, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(100, 255, 218, 0.1);
}

.project-image-container {
  position: relative;
  padding-top: 56.25%; /* 16:9 aspect ratio */
  overflow: hidden;
}

.project-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.project-card:hover .project-image {
  transform: scale(1.05);
}

.project-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(26, 26, 26, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.project-card:hover .project-overlay {
  opacity: 1;
}

.view-project-btn {
  padding: 0.75rem 1.5rem;
  background-color: #64ffda;
  color: #1a1a1a;
  text-decoration: none;
  border-radius: 4px;
  font-family: 'Roboto Mono', monospace;
  font-weight: 500;
  transition: transform 0.2s ease;
}

.view-project-btn:hover {
  transform: scale(1.05);
}

.project-content {
  padding: 1.5rem;
}

.project-title {
  color: #64ffda;
  font-family: 'Roboto Mono', monospace;
  font-size: 1.25rem;
  margin: 0 0 1rem 0;
}

.project-description {
  color: #ffffff;
  line-height: 1.6;
  margin-bottom: 1rem;
}

.project-tags {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  margin-top: 1rem;
}

.project-tag {
  background-color: rgba(100, 255, 218, 0.1);
  color: #64ffda;
  padding: 0.25rem 0.75rem;
  border-radius: 15px;
  font-size: 0.85rem;
  font-family: 'Roboto Mono', monospace;
}

/* Project Categories */
.project-category {
  margin-bottom: 4rem;
}

.category-title {
  font-size: 1.75rem;
  color: #64ffda;
  margin-bottom: 2rem;
  text-align: center;
  font-family: 'Roboto Mono', monospace;
  position: relative;
  display: inline-block;
}

.category-title::after {
  content: '';
  display: block;
  height: 2px;
  width: 60%;
  margin: 0.5rem auto 0;
  background-color: rgba(100, 255, 218, 0.3);
}

/* ✅ Skills Section */
.skills-section {
  display: block;     /* ✅ force full width block element */
  max-width: 1200px;
  margin: 4rem auto 0 auto; /* ✅ spacing above, centered */
  width: 100%;
  text-align: center;
}

.skills-bubble-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 2rem;
  gap: 2rem;
  padding: 3rem 2rem;
  max-width: 900px;
  margin: 0 auto;
}

.skill-bubble {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.15);
  border: 1px solid rgba(100, 255, 218, 0.25);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.35s ease, background 0.3s ease, box-shadow 0.3s ease, border 0.3s ease;
  cursor: pointer;
  position: relative;
}

.skill-bubble:hover {
  transform: scale(1.25);
  background: rgba(100, 255, 218, 0.18);
  border-color: #64ffda;
  box-shadow: 0 0 10px rgba(100, 255, 218, 0.4);
  z-index: 2;
}

.skill-bubble-icon {
  width: 42px;
  height: 42px;
  transition: transform 0.3s ease, filter 0.3s ease;
}

.skill-bubble:hover .skill-bubble-icon {
  transform: scale(1.15);
  filter: none;
}

.skill-bubble.active {
  background: #64ffda;
  border: 1px solid #64ffda;
  transform: scale(1.35);
  box-shadow: 0 0 15px rgba(100, 255, 218, 0.6);
}

.skill-bubble.active .skill-bubble-icon {
  filter: brightness(0) invert(1);
}

.active-skill-name {
  margin-bottom: 1.5rem;
  text-align: center;
  font-family: 'Roboto Mono', monospace;
  font-size: 1.2rem;
  color: #64ffda;
  font-weight: 600;
}

.skill-tooltip {
  position: fixed;
  background: #1a1a1a;
  border: 1px solid #64ffda;
  color: #64ffda;
  padding: 0.4rem 0.8rem;
  border-radius: 6px;
  font-family: 'Roboto Mono', monospace;
  font-size: 0.9rem;
  pointer-events: none;
  z-index: 999;
  white-space: nowrap;
  box-shadow: 0 4px 10px rgba(0,0,0,0.3);
  transition: opacity 0.15s ease;
}

